import React from 'react'
const SidebarSearch=()=>{
    return(
        <div className="sidebar_search_widget">
            <div className="blog_search_widget">
                <div className="input-group">
                    <input type="text" className="form-control" placeholder="Search Here" aria-label="Recipient's username" aria-describedby="button-addon2"/>
                    <div className="input-group-append">
                        <button className="btn btn-outline-secondary" type="button" id="button-addon2"><span className="flaticon-magnifying-glassNamclassName"></span></button>
                    </div>
                </div>
            </div>
		</div>
    )
}
export default SidebarSearch