import React,{useState} from 'react'
import {graphql, Link, useStaticQuery} from 'gatsby'
import Img from "gatsby-image"
import LightBox from "../../../../Global/App/Lightbox/Lightbox.component"
import styled from 'styled-components'
import { Nav, Tab } from 'react-bootstrap'
import SidebarSearch from '../../../../Global/App/SidebarWidgets/SidebarSearch.component'
import BreadcrumContent from '../../../../Global/Pages/BreadcrumbContent.component'
import Review from '../../../../Global/Pages/Review.component'
import Loadable from '@loadable/component'

const OwlCarousel = Loadable(() => import('react-owl-carousel3'))

const options = {
    loop: true,
    nav: false,
    dots: true,
    autoplayHoverPause: false,
    autoplay: false,
    margin: 30,
    smartSpeed: 1200,
    navText: [
      	'<i class="fa fa-arrow-left"></i>',
      	'<i class="fa fa-arrow-right"></i>'
    ],
    responsive: {
        0: {
            items: 1,
            center: false
        },
        480:{
            items:1,
            center: false
        },
        600: {
            items: 1,
            center: false
        },
        768: {
            items: 2
        },
        992: {
            items: 1
        },
        1200: {
            items: 1
        },
        1280: {
            items: 1
        }
    }
}
const ImgContainer = styled.div`
    cursor:pointer;
	border-radius: 50%;
    background-color: #ffffff;
    height: 50px;
    position: absolute;
    right: 10px;
    text-align: center;
    top: 10px;
    width: 50px;
`
const Icon = styled.span `
	color: #484848;
    font-size: 25px;
	line-height: 2;
	`
const RelatedProduct = ({children}) =>{
	return(
		<div className="row mb30">
			<div className="col-lg-12">
				<h4 className="mt25">Related products</h4>
			</div>
			{children}
	</div>
)
}
const ProductItem=({title, price, children})=>{
		return(
			<div className="col-sm-6 col-lg-6">
				<div className="shop_grid">
					<div className="thumb">
						{children}
						<div className="tag">Sale</div>
					</div>
					<div className="details">
						<h4 className="item-tile">{title} <span className="price">${price}</span></h4>
						<button className="btn btn-thm add_to_cart">Add to cart</button>
					</div>
				</div>
			</div>
		)
	}
const TermsConditionWidget=()=>{
		return(
			<div className="terms_condition_widget">
				<h4 className="title">Product Categories</h4>
				<div className="widget_list">
					<ul className="list_details">
						<li><Link to="#"><i className="fa fa-caret-right mr10"></i>Clothing</Link></li>
						<li><Link to="#"><i className="fa fa-caret-right mr10"></i>Decor</Link></li>
						<li><Link to="#"><i className="fa fa-caret-right mr10"></i>Music</Link></li>
						<li><Link to="#"><i className="fa fa-caret-right mr10"></i>Uncategorized</Link></li>
						<li><Link to="#"><i className="fa fa-caret-right mr10"></i>House</Link></li>
					</ul>
				</div>
			</div>
		)
}
const SideBarProductItem=({title,price,children})=>{
		return(
		<div className="media">
			{children}
			<div className="media-body">
				<h5 className="mt-0 post_title">{title}</h5>
				<Link to="#">${price}</Link>
			</div>
		</div>
		)
	}
const SideBarProducts = ({children}) =>{
	return(
		<div className="sidebar_recent_product">
			<h4 className="title">Recent Products</h4>
			{children}
		</div>
	)
}

const ShopSingle =()=>{
	const [showLightbox, setShowLightbox] = useState(false)
	const [selectedImage, setSelectedImage] = useState(null)

	const handleOpen = i => e => {
		setShowLightbox(true)
		setSelectedImage(i)
	}
	const handleClose = () => {
		setShowLightbox(false)
		setSelectedImage(null)
	}
	const handlePrevRequest = (i, length) => e => {
		setSelectedImage((i - 1 + length) % length)
	}
	const handleNextRequest = (i, length) => e => {
		setSelectedImage((i + 1) % length)
	}
	const data = useStaticQuery(graphql`
	query{
		source: allFile(filter: { absolutePath: { regex:"/shop/ss/" } }) {
            edges {
              node {
                childImageSharp {
                  fluid(maxHeight: 500) {
                     ...GatsbyImageSharpFluid
					presentationWidth
					originalImg
                  }
                }
              }
            }
          }
		shop1: file(relativePath: {eq: "shop/1.png"}) {
        id
        childImageSharp {
          fluid {
            originalImg
          }
        }
		}
		shop2: file(relativePath: {eq: "shop/2.png"}) {
			id
			childImageSharp {
			fluid {
				originalImg
			}
			}
		}
	}`)
	const images = data.source.edges
    return(
        <section className="shop-single-content bgc-f7">
		
		<div className="container">
			<div className="row">
				<div className="col-xl-6">
					<BreadcrumContent title="Shop Sıngle" active='Shop List'/>
				</div>
			</div>
			<div className="row">
				<div className="col-lg-8">
					<div className="single_product_grid row">
					<OwlCarousel className="single_product_slider col-lg-6  owl-carousel owl-theme" {...options}> 
							<div className="item">
								<div className="sps_content">
									<div className="thumb">
										<div className="single_product">
											<div className="single_item">
												<div className="thumb">
												<img className="img-fluid" src={images[0].node.childImageSharp.fluid.originalImg} alt="ss1.png"/>
											</div>
											</div>
											<ImgContainer className="sproduct_popup" onClick={handleOpen(0)} >
												<Icon className="flaticon-zoom-in"></Icon>
											</ImgContainer>
										</div>
									</div>
								</div>
							</div>
							<div className="item">
								<div className="sps_content">
									<div className="thumb">
										<div className="single_product">
											<div className="single_item">
												<div className="thumb">
												<img className="img-fluid" src={images[1].node.childImageSharp.fluid.originalImg} alt="ss1.png"/>
                                                </div>
											</div>
											<ImgContainer className="sproduct_popup" onClick={handleOpen(1)} >
												<Icon className="flaticon-zoom-in"></Icon>
											</ImgContainer>
										</div>
									</div>
								</div>
							</div>
							<div className="item">
								<div className="sps_content">
									<div className="thumb">
										<div className="single_product">
											<div className="single_item">
												<div className="thumb">
												<img className="img-fluid" src={images[0].node.childImageSharp.fluid.originalImg} alt="ss1.png"/>
                                                </div>
											</div>
											<ImgContainer className="sproduct_popup" onClick={handleOpen(2)} >
												<Icon className="flaticon-zoom-in"></Icon>
											</ImgContainer>
										</div>
									</div>
								</div>
							</div>
						</OwlCarousel>
						{showLightbox && selectedImage !== null && (
							<LightBox
								images={images}
								handleClose={handleClose}
								handleNextRequest={handleNextRequest}
								handlePrevRequest={handlePrevRequest}
								selectedImage={selectedImage}
							/>
							)}
						<div className="col-lg-6">
							<div className="sps_content">
								<div className="content">
									<div className="shop_single_product_details">
										<h4 className="title">Hoodie</h4>
										<div className="sspd_price mb25">$13,000</div>
										<p className="mb20">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Fusce aliquet urna ac leo lacinia, posuere bibendum diam finibus. Suspendisse eu </p>
										<ul className="cart_btns ui_kit_button mb20">
											<li className="list-inline-item"><input placeholder="5" type="number"/></li>
											<li className="list-inline-item"><button type="button" className="btn"><span className="flaticon-shopping-bag pr5 fz20"></span> Add To Cart</button></li>
										</ul>
										<ul className="sspd_sku mb30">
											<li><Link to="#">SKU: woo-album Category: Music</Link></li>
										</ul>
									</div>
								</div>
							</div>
						</div>
					</div>
					<div className="shop_single_tab_content mt30" >
					<Tab.Container defaultActiveKey="description">
						<Nav as='ul' className="nav nav-tabs">
							<Nav.Item as="li" className="nav-item">
							    <Nav.Link className="nav-link"eventKey="description">Description</Nav.Link>
							</Nav.Item>
							<Nav.Item as="li" className="nav-item">
							    <Nav.Link className="nav-link" eventKey="review">Reviews</Nav.Link>
							</Nav.Item>
						</Nav>
						<Tab.Content>
							<Tab.Pane eventKey="description" className="fade">
								<div className="product_single_content">
									<div className="mbp_pagination_comments">
										<div className="mbp_first media">
											<div className="media-body">
										    	<p className="mb25">Evans Tower very high demand corner junior one bedroom plus a large balcony boasting full open NYC views. You need to see the views to believe them. Mint condition with new hardwood floors. Lots of closets plus washer and dryer.</p>
										    	<p className="mt10 mb0">Fully furnished. Elegantly appointed condominium unit situated on premier location. PS6. The wide entry hall leads to a large living room with dining area. This expansive 2 bedroom and 2 renovated marble bathroom apartment has great windows. Despite the interior views, the apartments Southern and Eastern exposures allow for lovely natural light to fill every room. The master suite is surrounded by handcrafted milkwork and features incredible walk-in closet and storage space.</p>
											</div>
										</div>
									</div>
								</div>
							</Tab.Pane>
							<Tab.Pane eventKey="review" className="review">
								<Review/>
							</Tab.Pane>
						</Tab.Content>
					</Tab.Container>
					</div>
					<RelatedProduct>
						<ProductItem title="Album" price="13,000">
							<img className="img-fluid w100" src={data.shop1.childImageSharp.fluid.originalImg} alt="1.png"/>
						</ProductItem>
						<ProductItem title="Beanie" price="13,000">
							<img className="img-fluid w100" src={data.shop2.childImageSharp.fluid.originalImg} alt="1.png"/>
						</ProductItem>
					</RelatedProduct>
				</div>
				<div className="col-lg-4">
					<SidebarSearch />
					<TermsConditionWidget/>
					<SideBarProducts>
					<SideBarProductItem title="Album" price="13000">
						<img className="align-self-start mr-3" src={images[0].node.childImageSharp.fluid.originalImg} alt="ss1.png"/>
					</SideBarProductItem>
					<SideBarProductItem title="Beanie" price="13000">
						<img className="align-self-start mr-3" src={images[1].node.childImageSharp.fluid.originalImg} alt="ss1.png"/>
					</SideBarProductItem>
					<SideBarProductItem title="T-Shirt" price="13000">
						<img className="align-self-start mr-3" src={images[2].node.childImageSharp.fluid.originalImg} alt="ss1.png"/>
					</SideBarProductItem>
					</SideBarProducts>
				</div>
			</div>
		</div>
	</section>
    )
}
export default ShopSingle